import * as Util from "util";
import type { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { isBang } from "../sdk";
import UIObject from "./base";
import MessageUI from "../ui/message";

function splitMessages(text: string) {
    let messages = [];
    let last_separator = 0;
    let mode = "message";

    for (let i = 0; i < text.length; i++) {
        if (mode === "message") {
            if (text[i] == ',') {
                messages.push(text.slice(last_separator, i).trim());
                last_separator = i + 1;
                i += 1;
            } else if (text[i] === '"') {
                mode = "string";
            }
        } else {
            let escaped = i > 0 && text[i - 1] === "\\";
            if (!escaped && text[i] === '"') {
                mode = "message";
            }
        }
    }

    messages.push(text.slice(last_separator).trim());
    return messages;
}

function isWhitespace(text: string) {
    return "\n\r\t ".includes(text);
}

function tokenizeMessage(text: string) {
    let tokens = [];
    let last_whitespace = 0;
    let mode = "message";

    for (let i = 0; i < text.length; i++) {
        if (mode === "message") {
            let last_not_space = i > 0 && !isWhitespace(text[i-1]);
            if (last_not_space && isWhitespace(text[i])) {
                tokens.push(text.slice(last_whitespace, i).trim());
                last_whitespace = i;
            } else if (text[i] === '"') {
                mode = "string"
            }
        } else {
            let escaped = i > 0 && text[i - 1] === "\\";
            if (!escaped && text[i] === '"') {
                mode = "message";
            }
        }
    }

    // We only push a final time if there's no trailing whitespace
    if (!isWhitespace(text[text.length-1]))
        tokens.push(text.slice(last_whitespace).trim());

    // Trim leading and trailing double quotes and correct quote escapement
    for (let i = 0; i < tokens.length; i++) {
        let length = tokens[i].length;
        let start: number = tokens[i][0] === '"' ? 1 : 0;
        let end: number = tokens[i][length-1] === '"' ? length - 1 : length;
        tokens[i] = tokens[i].slice(start, end).replace('\\"', '"');
    }

    let output: Message = new Message(...tokens.map(value => Number(value) || value));

    return output;
}

const FORMATTER_REGEX = /^\$[1-9][0-9]*$/;

function extractFormatters(message: any[]) {
    let formatters = [];
    for (let i = 0; i < message.length; i++) {
        const token = message[i];
        if ((typeof token === "string") && FORMATTER_REGEX.test(token)) {
            let inlet_index = parseInt(token.slice(1)) || 0;
            formatters.push({inlet_index, position: i});
        }
    }
    return formatters;
}

export type Token = number | string;

export class Message extends Array<Token> {
    startsWith(value: Token) {
        if (this.length) {
            return this[0] === value;
        }
        return false;
    }

    endsWith(value: Token) {
        if (this.length) {
            return this[this.length-1] === value;
        }
        return false;
    }

    // Produces a function that iterates over two lists,
    // performs the given operation on any pair of numbers,
    // and returns the shortest collection between the two
    arithmetic(op: (a: number, b: number) => number) {
        return (other: Message) => {
            const result = new Message();
            const minLength = Math.min(this.length, other.length);

            for (let i = 0; i < minLength; i++) {
                if (typeof this[i] === 'number' && typeof other[i] === 'number') {
                    result.push(op(this[i] as number, other[i] as number));
                } else {
                    result.push(this[i]);
                }
            }

            return result;
        }
    }
}

export function isMessage(value: any) {
    return value instanceof Message;
}

interface Formatter {
    inlet_index: number;
    message_index: number;
    position: number;
}

export interface IS {
    buffer: Message[];
    formatters: Formatter[];
    editing: boolean;
}

export default class message extends UIObject<{ text: string }, {}, [any, any], [any], [any], {}, { text: string }> {
    static description = "Message";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "anything",
            description: "Trigger output the message"
        },
        // {
        //     isHot: false,
        //     type: "anything",
        //     description: "Set the message"
        // }
    ];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "Message to send"
    }];
    static UI = MessageUI;
    _: IS = { buffer: [], editing: false, formatters: [] };
    handleUpdateArgs = (args: any[]) => {
        if (typeof args[0] !== "undefined") {
            this.setData({ text: this.stringify(args[0]) });
            this._.buffer = this.parse(args[0]);
            this._.formatters = [];
            if (Array.isArray(this._.buffer)) {
                for (let i = 0; i < this._.buffer.length; i++) {
                    this._.formatters.push(...extractFormatters(this._.buffer[i]).map(
                        ({inlet_index, position}) => {return {inlet_index, position, message_index: i};}));
                }
            }
        } else {
            this._.buffer = [];
            this._.formatters = [];
        }
        this.updateUI({ text: this.data.text });
    };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = message.inlets.length;
            this.outlets = 1;
        });
        this.on("postInit", () => {
            const args = this.box.args;
            if (typeof this.data.text === "string") this.handleUpdateArgs([this.data.text]);
            else if (typeof args[0] !== "undefined") {
                if (typeof this.data.text !== "string") {
                    // this.setData({ text: this.stringify(args[0]) });
                    // this._.buffer = args[0];
                    this.handleUpdateArgs(args);
                }
            } else {
                this.setData({ text: "" });
                this._.buffer = [];
                this._.formatters = [];
            }
        });
        this.on("updateArgs", this.handleUpdateArgs);
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                this.produceOutput(data);
            }
            // else if (inlet === 1) this.handleUpdateArgs([data]);
        });
    }
    produceOutput = (data?: any) => {
        if (Array.isArray(this._.buffer)) {

            // Format the elements
            if (data && !isBang(data) && this._.formatters.length) {
                // we either expect a single value or a message (i.e. array)
                if (data instanceof Message) {
                    for (let i = 0; i < data.length; i++)
                        this.formatMessage(i + 1, (message, pos) => this._.buffer[message][pos] = data[i]);
                } else {
                    for (let b = 0; b < this._.buffer.length; b++) {
                        this.formatMessage(1, (message, pos) => this._.buffer[message][pos] = data);
                    }
                }
            }

            this._.buffer.forEach(m => this.outlet(0, m));
        } else {
            this.outlet(0, this._.buffer);
        }
    }
    parse(o: string) {
        return splitMessages(o).map(m => tokenizeMessage(m));
    }
    stringify(o: any) {
        return o.toString();
    }
    formatMessage(target_inlet_index: number, closure: (message_index: number, position: number) => void) {
        for (let {inlet_index, message_index, position} of this._.formatters) {
            if (inlet_index === target_inlet_index)
                closure(message_index, position);
        }
    }
}
